* {
  padding: 0px;
  margin: 0px;
  border: 0px;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  min-width: 320px;
}
body {
  background-color: #2E3DBE;
  color: #414141;
  line-height: 1;
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
input,
button,
textarea {
  font-family: 'Open Sans', sans-serif;
  font-size: inherit;
}
button {
  cursor: pointer;
  color: inherit;
  background-color: inherit;
}
a {
  color: inherit;
}
a:link,
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
ul li {
  list-style: none;
}
img {
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}
/*
(i) Стили будут применяться ко
всем классам содержащим *__container
Например header__container, main__container и т.д.
*/
.container {
  box-sizing: content-box;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 479.98px) {
  .container {
    padding: 0 40px;
  }
}
@media (min-width: 767.98px) {
  .container {
    padding: 0 60px;
  }
}
.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.just-validate-error-label {
  font-family: 'Open Sans', sans-serif !important;
  font-size: 12px !important;
  font-weight: 600 !important;
  line-height: 16px !important;
  color: #E15554 !important;
}
.just-validate-error-field {
  border: 1px solid #E15554 !important;
}
.hero {
  padding: 36px 0 36px;
}
.hero__img {
  width: 100%;
  margin-bottom: 48px;
}
@media (min-width: 767.98px) {
  .hero__img {
    margin-bottom: 60px;
  }
}
.hero .form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  background-color: #ffffff;
  min-height: 502px;
  max-width: 552px;
  margin: 0 auto;
  padding: 30px;
  box-shadow: 0px 8px 40px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}
@media (min-width: 479.98px) {
  .hero .form {
    gap: 20px;
    padding: 48px;
  }
}
.hero .form__items {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
@media (min-width: 479.98px) {
  .hero .form__items {
    gap: 20px;
  }
}
.hero .form__title {
  font-size: 24px;
  font-weight: 700;
  line-height: 32px;
  color: #414141;
}
.hero .form__item {
  display: flex;
  flex-direction: column;
}
.hero .form__label {
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
  color: #414141;
  margin-bottom: 4px;
}
.hero .form__input {
  width: 100%;
  height: 32px;
  padding: 8px;
  margin-bottom: 4px;
  border: 1px solid #AAAAAA;
  border-radius: 4px;
}
.hero .form__input:hover,
.hero .form__input:focus {
  border: 1px solid #808080;
}
.hero .form__input::placeholder {
  font-size: 14px;
  font-weight: 400;
  line-height: 16px;
  color: #959595;
}
.hero .form__description {
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  color: #565656;
}
.hero .form__description > a {
  font-weight: 600;
  line-height: 14px;
  color: #45688F;
  text-decoration: underline;
  transition: all 0.3s;
}
.hero .form__description > a:hover,
.hero .form__description > a:focus {
  color: #6683A3;
  opacity: 1;
}
.hero .form__btn {
  width: 100%;
  min-height: 40px;
  border: none;
  cursor: pointer;
  background-color: #FF9000;
  color: #ffffff;
  font-size: 14px;
  font-weight: 600;
  line-height: 16px;
  border-radius: 4px;
  transition: all 0.3s;
}
.hero .form__btn:hover,
.hero .form__btn:focus {
  background-color: #FFA42E;
  opacity: 1;
}
.hero .form__social-item {
  position: relative;
}
.hero .form__social-description {
  background-color: #ffffff;
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
  color: #808080;
  text-align: center;
  max-width: 180px;
  margin: 0 auto;
  z-index: 2;
  position: relative;
}
@media (min-width: 767.98px) {
  .hero .form__social-description {
    max-width: 260px;
  }
}
.hero .form__social-decor {
  background-color: #D4D4D4;
  height: 1px;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
  display: block;
  z-index: 1;
}
.hero .form__social-icons {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
@media (min-width: 767.98px) {
  .hero .form__social-icons {
    flex-direction: row;
  }
}
.hero .form__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #AAAAAA;
  border-radius: 4px;
  transition: all 0.3s;
  font-size: 14px;
  font-weight: 600;
  line-height: 24px;
  color: #414141;
}
.hero .form__social-link:hover,
.hero .form__social-link:focus {
  border: 1px solid #FF9000;
  opacity: 1;
}
.hero .form__social-img {
  object-fit: cover;
  object-position: left;
}
